// Common styles of the project

html,
body {
    height: 100%;
    font-family: "Ubuntu";
    font-family: Ubuntu;
    font-weight: 700;
}

button {
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
}

a {
    text-decoration: none;
    color: black;
}

.page {
    overflow-x: hidden;
}

.page__wrapper {
    min-height: 100%;
    margin-bottom: -50px;

}

* html .page__wrapper {
    height: 100%;

}

.page__buffer {
    height: 50px;
}

.logo {
    background-image: url(../img/content/logo.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

@media (max-width: 1360px) {

    .page__wrapper {

        .main,
        .paralax {
            width: 100%;
            min-height: 90vh;

        }

        .l {
            left: -230px;
        }

        .r {
            left: 870px;
        }
    }
}

@media (max-width: 370px) {

    .page__wrapper {
        max-width: 370px;
        min-width: 321px;

        .mobile-menu {
            padding: 15px;
        }

        .header__inner {
            padding: 0;
            padding-left: 27px;
            height: 50px;
        }

        .burger-menu {
            display: flex;
        }

        .header__inner-nav,
        .profile,
        .header__logo {
            display: none;
        }

        .mobile-menu.active {
            display: block;
            position: absolute;
            z-index: 1;
            width: -webkit-fill-available;
        }

        .images-paralax__item {
            display: none;
        }

        .content__headline-span {
            font-size: 20px;
            line-height: 100%;
            letter-spacing: 0%;

        }

        .main {
            padding-left: 0;
            padding-right: 0;
            width: -webkit-fill-available;
        }
        .main__content-cards .card:last-child .card__message{
            top: 25%;
            right: 50%;
        }
        .content__banner {
            display: none;
        }

        .main__content {
            width: inherit;
            padding-left: 20px;
            padding-right: 20px;
        }

        // 
        .main__content-cards {
            position: relative;
            overflow: hidden;
            width: inherit;
            margin-top: 23px;
        }

        .cards__content-inner {
            display: flex;
            transition: transform 0.5s ease;
            flex-direction: row;
            flex-wrap: nowrap;
            gap: 50px;
            justify-content: space-between;
            cursor: grab;
            overflow-x: auto;
            scroll-snap-type: x mandatory;
        }

        .cards__content-inner::-webkit-scrollbar {
            width: 0;
        }

        .main__content-cards {
            .card {
                min-width: max-content;
                max-height: 59px;
                height: 170px;
                display: flex;
                flex-direction: row;
                justify-items: center;
                align-items: center;
                scroll-snap-align: start;
                position: relative;
                padding-left: 50px;

                .card__inner {
                    height: -webkit-fill-available;
                    flex-direction: row-reverse;
                    

                    .card__button {
                        display: none;
                    }
                }
            }

            .card:first-child {}

            .card__message {
                display: flex;
                top: 25%;
                right: 42%;
                position: absolute;

                .card__messag-span {
                    width: max-content;
                }
            }

            .card__inner {
                width: 100%;
                height: 118px;
                border-radius: 10px;
                background: rgba($color: #9564AA, $alpha: 1.0);
                border: 2px solid #9564AA;
                display: flex;
                flex-direction: column;
                gap: 11px;
                padding-top: 0;
                min-width: 203px;
                padding-right: 30px;
                align-items: center;

                .card__text-price {
                    color: white;
                }
            }

            .card:last-child {
                width: -webkit-fill-available;
                height: 59px;
                .card__message {
                    // left: 9%;
                }

                .card__inner {
                    flex-direction: row;
                    justify-content: flex-start;
                    background: #aa6464;
                    border: rgba(170, 100, 100, 1);
                    flex-direction: row-reverse;
                    border-radius: 10px 10px 10px 10px;
                    height: 59px;
                }
            }

            .sim-slider-dots {
                display: flex;
                justify-content: center;
            }

            .dot {
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background-color: rgba(255, 197, 0, 1);
                margin: 0 5px;
                transition: background-color 0.3s ease;
            }

            .dot.active {
                background-color: orange;
            }

            .card:last-child {
                padding-right: 50px;
            }
        }

        .premium__banner {
            background-size: 81px 67px;
            background-position: 50% 0;
        }

        .premium__banner-item {
            width: max-content;
            height: max-content;
            flex-direction: row;

        }

        .premium__banner-list {
            flex-wrap: nowrap;
            flex-direction: column;
            gap: 10px;
            position: relative;
            top: 79px;
        }

        #message {
            transform: none;
        }

        #message2 {
            transform: none;
        }

        #follow {
            transform: none;
        }

        #gifts {
            transform: none;
        }

        #show {
            transform: none;
        }

        #settings {
            transform: none;
        }

    }
}