.content {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.banner__inner-text {
    display: flex;
    flex-direction: row;
    gap: 35px;
    align-items: center;
}

.banner__inner-timer {
    font-family: Ubuntu;
    font-weight: 400;
    font-size: 40px;
    line-height: 100%;
    letter-spacing: 0%;
    text-align: center;
    color: #000000;
    background-color: #fff;

    border-radius: 5px;
    padding-top: 5px;
    padding-right: 20px;
    padding-bottom: 5px;
    padding-left: 20px;
    display: flex;
    gap: 10px;

    span {
        font-family: Ubuntu;
        font-weight: 400;
        font-size: 40px;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        color: #000000;

    }
}

.content__headline-span {
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    letter-spacing: 0%;
    text-shadow: #00000040 4px 4px;

}

.content__banner {
    width: 100%;
    // height: 82px;
    padding: 13px 0;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #C15CA1;

}

.banner__inner-headline {
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 40px;
    line-height: 100%;
    letter-spacing: 4%;
    text-align: center;
    color: #FFCF2D;
    text-shadow: 1px 0 0px #ffffff,
        0 1px 0px #ffffff,
        -1px 0 0px #ffffff,
        0 -1px 0px #ffffff;

}

.banner__inner-paragraph {
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0%;
    text-align: center;
    color: white;
}

.cards__content-inner {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.main__content-cards {
    .card {
        width: 150px;
        height: 170px;
        display: grid;
        justify-items: center;
        align-items: end;
    }

    .card:last-child {
        width: -webkit-fill-available;
        height: 123px;

        .card__inner {
            flex-direction: row;
            justify-content: center;
            background: #AA646433;
            height: 74px;
            border: 2px solid rgba(170, 100, 100, 1);
        }

        .card__message {
            top: 70%;
        }
    }

    .card__message {
        width: 123px;
        height: 23px;
        border-radius: 5px;
        padding-top: 3px;
        padding-right: 10px;
        padding-bottom: 4px;
        padding-left: 10px;
        background: #FFCF2D;
        // none
        display: none;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        position: relative;
        top: 50%;
    }

    .card__messag-span {
        font-family: Ubuntu;
        font-weight: 700;
        font-size: 14px;
        line-height: 100%;
        letter-spacing: 0%;
        color: #000000;
    }

    .card__inner {
        width: 100%;
        height: 118px;
        border-radius: 10px;
        background: rgba($color: #9564AA, $alpha: 0.1);
        border: 2px solid #9564AA;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 11px;
        padding-top: 15px;
        // opacity: 0.1;
    }

    .card__text {
        display: flex;
    }

    .card__text-price {
        font-family: Ubuntu;
        font-weight: 700;
        font-size: 24px;
        line-height: 100%;
        letter-spacing: 0%;
        text-align: center;
        color: #000000;
        display: inline-flex;
        gap: 5px;
        align-items: center;
    }

    .card__text-price::after {
        content: '';
        display: inline-block;
        width: 17px;
        height: 17px;
        background: url(../../static/img/content/icons/credit.svg) top / cover no-repeat;
    }

    .card__button {
        width: 120px;
        height: 52px;
        background: #FFFFFF;
        color: #00000020;
        border-radius: 5px;
    }
}

.main__content-buttontry {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 34px;
}

.main__content-try {
    display: block;
    width: 280px;
    height: 55px;
    border-radius: 10px;
    background: rgba(255, 207, 45, 1);
    color: #000000;
}

.main__content-acceptlabel {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    font-family: Ubuntu;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    letter-spacing: 0%;
    margin-top: 12px;
}

/* Hide the browser's default checkbox */
.main__content-acceptlabel input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    border-radius: 5px;

    background-color: #eeee;
}

/* On mouse-over, add a grey background color */
.main__content-acceptlabel:hover input~.checkmark {
    background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.main__content-acceptlabel input:checked~.checkmark {
    display: flex;
    background: #9564AA;
    // align-items: center;
    // justify-content: center;

}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

/* Show the checkmark when checked */
.main__content-acceptlabel input:checked~.checkmark:after {
    display: block;
}

/* Style the checkmark/indicator */
.main__content-acceptlabel .checkmark:after {
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 2px;
    left: 6px;
}

.main__content-acceptlink {
    color: #9564AA;
}