.main__content-acceptlabel {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Ubuntu;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0%;
  margin-top: 12px;
}

/* Hide the browser's default checkbox */
.main__content-acceptlabel input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  border-radius: 5px;
  background-color: rgba(238, 238, 238, 0.9333333333);
}

/* On mouse-over, add a grey background color */
.main__content-acceptlabel:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
.main__content-acceptlabel input:checked ~ .checkmark {
  display: flex;
  background: #9564AA;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.main__content-acceptlabel input:checked ~ .checkmark:after {
  display: block;
}

/* Style the checkmark/indicator */
.main__content-acceptlabel .checkmark:after {
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 2px;
  left: 6px;
}

