.premium__banner-item {
  width: 108px;
  height: 96px;
  color: rgb(149, 100, 170);
  font-family: Ubuntu;
  font-weight: 700;
  font-size: 12px;
  line-height: 100%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
}

#message {
  transform: translateX(50px) translateY(-15px);
}

#message::before {
  display: block;
  content: "";
  background-image: url(../../static/img/content/icons/message.svg);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  width: 50px;
  height: 50px;
  background-color: rgba(149, 100, 170, 0.1);
  border-radius: 50px;
}

#message2 {
  transform: translateX(50px) translateY(15px);
}

#message2::before {
