.container {
    max-width: 1000px;

}
.wrapper{
    position: absolute;
}
.paralax {
    min-height: 55vh;
    
}

.paralax__body {
    position: sticky;
    width: 100%;
    // height: 100%;
    left: 0;
}

.paralax__container {
    position: relative;
    z-index: 2;
}

.paralax__images {
    // position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    display: flex;
    justify-content: space-between;
}

// .images__paralax {}

.images-paralax__item {
    position: absolute;
    // width: 100%;
    // height: 100%;
    top: 100px;
    left: 0;
    
}
.r{
    left: 1259px;
}
.images-paralax__bubles {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    
}

.images-paralax__bubles-l {
    background: url(../../static/img/content/bubles-l.svg) top / cover no-repeat;
    width:638px;
    height: 638px;
    top: 0;
    transform: rotate(-55.73 deg);
}

.images-paralax__bubles-r {
    background: url(../../static/img/content/bubles-r.svg) top / cover no-repeat;
    width:638px ;
    height: 638px;
    // position: relative;
    // left: 1259px;
    transform: rotate(-135.54 deg);
    
}