.profile__user {
    width: 228px;
    height: 727px;
    border-radius: 10px;
    background: #9564AA1A;
    border: 2px solid #9564AA33;
    // position: absolute;
    padding: 19px;
    
    
}
.profile__user-inner{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 22px;
}
// .profile {}

.profile__user-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 15px;
}

// .header__profile {}

.header__profile-info {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.header__profile-avatar {
    width: 49px;
    height: 49px;
    border: 2px solid #FFFFFF;
    border-radius: 50px;
}

.avatar__img {
    background: url(../../static/img/content/avatar.png) top / contain no-repeat;
}

.header__profile-name {
    font-family: Ubuntu;
    font-weight: 700;
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0%;

}

.profile__button-premium {
    width: 86px;
    height: 21px;
    border-radius: 5px;
    padding: 5px 10px;
    text-transform: uppercase;
    font-family: Ubuntu;
    font-weight: 400;
    font-size: 10px;
    line-height: 100%;
    letter-spacing: 0%;

}

.premium__button {
    background: #684677;
    color: white;
}

.section__line {
    display: flex;
    width: 100%;
    height: 2px;
    opacity: 0.2;
    background: #9564AA;
}

// .profile__user-navigation {}

// .navigation__profile {}

// .navigation__profile-links {}

.profile__link-list {
    display: grid;
    gap: 26px;
    padding-left: 12px;
}

.profile__link-item {
    display: inline-flex;
    align-items: center;
    gap: 29px;
    font-weight: 400;
}

.profile__link-item::before {
    display: inline-block;
    width: 19px;
    height: 19px;
}

#itemid1::before {
    content: '';
    background: url(../../static/img/content/icons/avatar.svg) top / contain no-repeat;
}

#itemid2::before {
    content: '';
    background: url(../../static/img/content/icons/photo.svg) top / contain no-repeat;
}

#itemid3::before {
    content: '';
