.header__inner {
    background-color: #9564AA;
    padding-left: 375px;
    padding-right: 375px;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.header__logo {
    display: flex;
    width: 114px;
    height: 55px;
}

.nav__list {
    display: flex;
    flex-direction: row;
    gap: 40px;
    align-items: center;

    .nav__list-link {
        color: white;
    }

    .item-but {
        background-color: #684677;
        border-radius: 10px;
        padding: 6px 13px;
        color: #FFCF2D;
        position: relative;
        display: inline-flex;
        align-items: center;
        white-space: nowrap;
        gap: 10px;
    }

    .item-but::after {
        content: "";
        display: inline-block;
        width: 18px;
        height: 15px;
        background-image: url(../../static/img/content/icon-premium.svg);
        background-repeat: no-repeat;
        background-size: contain;
    }

    .nav__list-item {
        height: min-content;

    }
}

.burger-menu {
    /* Скрыть по умолчанию */
    height: 20px;
    display: none;
    gap: 7px;
    width: 20px;
    flex-direction: column;

    span {
        display: block;
        width: 23px;
        height: 2px;
        background-color: #fff;
    }
}

.mobile-menu {
    display: none;
    background-color: #ffffff;
    border: 5px solid #9564AA;
    /* Скрыть мобильное меню по умолчанию */
    .nav__list {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .profile__link-list{
        justify-items:flex-start;
        padding-left: 0;
    }
    
    .profile__link-item {
        color: rgb(0, 0, 0);
    }
    .navigation__profile-links{
        margin-left: 10px;
    }
}